.Toolbar{
    width: 100%;
    height: 56px;
    position:fixed;
    top:0;
    left:0;
    background-color: rgb(95, 7, 7);
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding:0px 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

.Logo{
    height: 80%;
} 

@media (max-width:549px)
{
    .DesktopOnly {
        display:none;
    }
}